export function licenseElement(config: IAppConfig) {
	/*
    * Create element to inject and return 
    <a href="https://verification.pagcorlicenses.ph?domain=betsihirbazi.com" target="_blank">
        <img src="https://verification.pagcorlicenses.ph/api/validate/logo?domain=betsihirbazi.com" width="100" />
    </a> 
    */

	const a = document.createElement('a');
	a.href = `https://verification.pagcorlicenses.ph?domain=${config.domain}`;
	a.target = '_blank';

	const img = document.createElement('img');
	img.src = `https://verification.pagcorlicenses.ph/api/validate/logo?domain=${config.domain}`;
	img.width = config.license?.image?.width || 100;

	a.appendChild(img);

	return a;
}

export function betcoInjection(config: IAppConfig) {
	// Find footer element example <li ng-show="conf.footer.license_logos &amp;&amp; !conf.footer.enableLicenseRowVersion" class="gamble-logos-j">
	// Or it can be located at ftr-partners-row-inner-bc
	let footer = document.querySelector('.ftr-partners-row-inner-bc');
	if (!footer) {
		footer = document.querySelector('.gamble-logos-j');
	}

	if (footer) {
		// Create element to inject
		const element = document.createElement('div');
		element.appendChild(licenseElement(config));

		// Inject element

		footer.appendChild(element);
	}
}

interface IAppConfig {
	domain: string;
	license?: {
		image: {
			width: number;
		};
	};
}

window.startShowLicense = function (config: IAppConfig) {
	betcoInjection(config);
};
